import React, {Component} from 'react';
import { Navbar, NavbarBrand, Nav, NavbarToggler,Collapse, NavItem, Button, Modal, ModalHeader, ModalBody, Form, FormGroup, Label, Input } from 'reactstrap';
import { NavLink} from 'react-router-dom';

class Header extends Component {

	constructor(props) {
		super(props);
		this.toggleNav = this.toggleNav.bind(this);
		this.toggleModal = this.toggleModal.bind(this);
		this.handleLogin = this.handleLogin.bind(this);
		this.state= {
			isNavOpen: false,
			isModalOpen: false
		};
	}

	toggleNav() {
		this.setState({
			isNavOpen: !this.state.isNavOpen
		});
	}

	toggleModal() {
		this.setState({
			isModalOpen: !this.state.isModalOpen
		});
	}

	handleLogin(event) {
		this.toggleModal();
		alert("Username: " + this.username.value + " Password: " + this.password.value + " Remember: " + this.remember.checked);
		event.preventDefault();
	}

	render() {
		return(
			<React.Fragment>
				<Navbar dark expand="md">
		          <div className="container">
		          	<NavbarToggler onClick={this.toggleNav} />
		            <NavbarBrand className="ml-0" href="/">
		            	<img src="assets/images/logo.png" height="50" width="130" alt="MyStore" />
		            </NavbarBrand>
		            <Collapse isOpen={this.state.isNavOpen} navbar >
			            <Nav navbar className="ml-auto">
			            	<NavItem>
			            		<NavLink className="nav-link ml-4" to="/home">
			            			<span className="fa fa-home fa-lg"></span> Home
			            		</NavLink>
			            	</NavItem>
			            	<NavItem>
			            		<NavLink className="nav-link ml-4" to="/aboutus">
			            			<span className="fa fa-info fa-lg"></span> AboutUs
			            		</NavLink>
			            	</NavItem>
			            	<NavItem>
			            		<NavLink className="nav-link ml-4" to="/menu">
			            			<span className="fa fa-list fa-lg"></span> Menu
			            		</NavLink>
			            	</NavItem>
			            	<NavItem>
			            		<NavLink className="nav-link ml-4" to="/contactus">
			            			<span className="fa fa-address-card fa-lg"></span> ContactUs
			            		</NavLink>
			            	</NavItem>
			            </Nav>
			            <Nav className="ml-4" navbar>
			            	<NavItem>
			            		<Button outline onClick={this.toggleModal}>
			            			<span className="fa fa-sign-in fa-lg"></span> Login
			            		</Button>
			            	</NavItem>
			            </Nav>
		            </Collapse>
		          </div>
		        </Navbar>
		        <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
		        	<ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
		        	<ModalBody>
		        		<Form onSubmit={this.handleLogin}>
		        			<FormGroup>
		        				<Label htmlFor="username">Username</Label>
		        				<Input type="text" id="username" name="username" innerRef={(input) => this.username = input} />
		        			</FormGroup>
		        			<FormGroup>
		        				<Label htmlFor="password">Password</Label>
		        				<Input type="text" id="password" name="password" innerRef={(input) => this.password = input} />
		        			</FormGroup>
		        			<FormGroup check>
		        				<Label check>
		        					<Input type="checkbox" name="remember" innerRef={(input) => this.remember = input} />
		        					Remember Me
		        				</Label>
		        			</FormGroup>
		        			<Button type="submit" value="submit" color="primary">Login</Button>
		        		</Form>
		        	</ModalBody>
		        </Modal>
			</React.Fragment>
		);
	}
}

export default Header;